import { Plugin, Query } from '@ali/4ever-cangjie';
import toolbar from './toolbar';
import menubar from './menubar';
import getLineHeight from './queries/getLineHeight';
import * as actions from './actions';
import onAction from './handlers/onAction';
import createRenderToolbar from './createRenderToolbar';
import { BiPlugin } from '../../types';

interface ILineHeightProps extends BiPlugin {
  toolbar: any;
  menubar: any;
}

const queries: Record<string, Query> = {
  getLineHeight,
};

function createLineHeightPlugin(config): ILineHeightProps {
  const lineHeightConfig = config.lineheight || {};
  const sideBarTriggerConfig = config.sideBarTrigger || {};
  return {
    toolbar: toolbar(lineHeightConfig, sideBarTriggerConfig),
    renderToolbar: createRenderToolbar(lineHeightConfig.locale, sideBarTriggerConfig),
    menubar: menubar(),
    queries,
    onAction,
    models: [],
  };
}

createLineHeightPlugin.actions = actions;
createLineHeightPlugin.queries = queries;

export default createLineHeightPlugin;
